import React, { Component } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import {
  Button,
  Card,
  CardBody,
  CardGroup,
  Col,
  Container,
  Form,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Row
} from "reactstrap";
import { Loader } from "react-overlay-loader";

//Css Files
import "./index.css";

import { colors, apiary } from "../../../api/constants/Constants";

//Images
import image1 from "../../../assets/icons/logo.png";

export default class LoginScreen extends Component<any, any> {
  constructor(props: any) {
    super(props);

    this.state = {
      data: [],
      isloading: false
    };
  }

  componentDidMount() {
    axios
      .get(apiary.getUserTypes, {
        headers: {
          "Access-Control-Allow-Origin": "*"
        }
      })
      .then(response => {
        let data = response.data;
        this.setState({ data: data.data });
      })
      .catch(function(error) {
        console.log(error);
      });
    let loginSeesionData = localStorage.getItem("sessionloginDetails");
    console.log({ loginSeesionData });
    if (loginSeesionData != null) {
      this.props.history.push("/dashboard");
    }
  }

  click_Login(e: any) {
    e.preventDefault();
    let isloading = true;
    this.setState({ isloading: isloading });
    let option = e.target.option.value;
    let email = e.target.email.value;
    let password = e.target.password.value;

    if (
      option == "Admin" &&
      email == "admin@gmail.com" &&
      password == "admin@1234"
    ) {
      let data = {};
      data.userId = 1;
      data.option = option;
      data.email = email;
      data.password = password;
      event.preventDefault();
      this.props.history.push({
        pathname: "/dashboard",
        state: { data: data }
      });
      localStorage.setItem("sessionloginDetails", JSON.stringify(data));
      isloading = false;
    } else {
      alert("Please enter correct email and password.");
      isloading = false;
    }
    this.setState({ isloading: isloading });
  }

  render() {
    return (
      <div className="app flex-row align-items-center">
        <Container>
          <Row className="justify-content-center">
            <Col md="6">
              <CardGroup>
                <Card className="p-4">
                  <CardBody>
                    <img src={image1} className="center" />
                    <h1 style={{ textAlign: "center" }}>St Michael's School</h1>
                    <div className="well">
                      <form onSubmit={this.click_Login.bind(this)}>
                        <div className="form-group">
                          <select
                            className="form-control browser-default custom-select"
                            name="option"
                          >
                            {this.state.data.map((item: any) => (
                              <option key={item.id} value={item.type}>
                                {item.type}
                              </option>
                            ))}
                          </select>
                        </div>
                        <div className="form-group">
                          <input
                            type="email"
                            name="email"
                            className="form-control"
                            placeholder="Email"
                            required
                          />
                        </div>
                        <div className="form-group">
                          <input
                            type="password"
                            name="password"
                            className="form-control"
                            placeholder="Password"
                            required
                          />
                        </div>
                        <div style={{ textAlign: "center" }}>
                          <div className="form-group">
                            <input
                              type="submit"
                              className="btn btn-primary"
                              value="Login"
                            />
                          </div>
                          <div className="form-group">
                            <a href="#" className="ForgetPwd">
                              Forget Password?
                            </a>
                          </div>
                        </div>
                      </form>
                    </div>
                  </CardBody>
                </Card>
              </CardGroup>
            </Col>
          </Row>
          <Loader fullPage loading={this.state.isloading} />
        </Container>
      </div>
    );
  }
}
